<template>
  <div class="web-audio">
    <h1>Synth Vue JS {{ ver }}</h1>
    <br /> 
    <select v-model="typeSelected" @change="onTypeChange">
      <option value="no">No sound</option>
      <option value="sine">Sine</option>
      <option value="sawtooth">Sawtooth</option>
      <option value="square">Square</option>
      <option value="triangle">Triangle</option>
    </select>

    <div>
      <p>Frequency: {{frequency}}Hz</p>
      <vue-slider v-model="frequency" :max=10000 @change="onFreqChange" />
    </div>

    <div>
      <p>Filter Gain: {{gain}}</p>
      <vue-slider v-model="gain" :max=1.0 :interval=0.01 @change="onGainChange" />
    </div>

    <div>
      <p>Filter Frequency: {{filterFrequency}}Hz</p>
      <vue-slider v-model="filterFrequency" :max=1000 @change="onFilterFreqChange" />
    </div>

    <br /> <br /> <br />
    <button v-show="!playingFile" v-on:click="playFile">Play (file)</button>
    <button v-show="playingFile" v-on:click="stopFile">Stop (file)</button>

  </div>
  
</template>

<script>  
  import VueSlider from 'vue-slider-component'
  import 'vue-slider-component/theme/antd.css'

  export default {
    name: 'WebAudio',
    components: {
      VueSlider
    },
    props: {
      ver: String,
    },
    data: function () {
      return {
        typeSelected: "no",
        frequency: 440,
        filterFrequency: 250,
        gain: 0,
        playFileCaption: "Play file",
        playingFile: false
      }
    },
    methods: {
      onTypeChange(event) {
        console.log(event.target.value);
        if (this.oscNode !== undefined) {
          this.oscNode.stop();
          this.oscNode = undefined;
        }
        if (event.target.value !== "no") {
          this.oscNode = window.audioContext.createOscillator()
          this.oscNode.type = event.target.value;
          this.oscNode.frequency.value = this.frequency;

          this.dryGainNode = window.audioContext.createGain();
          this.wetFilterGainNode = window.audioContext.createGain();
          this.gainMixerNode = window.audioContext.createGain();
          this.onGainChange();
          this.filterNode = window.audioContext.createBiquadFilter();
          console.log(this.filterNode.type);
          this.filterNode.frequency.value = this.filterFrequency;

          this.oscNode.connect(this.dryGainNode);
          this.oscNode.connect(this.wetFilterGainNode);
          this.wetFilterGainNode.connect(this.filterNode);
          this.filterNode.connect(this.gainMixerNode);
          this.dryGainNode.connect(this.gainMixerNode);
          this.gainMixerNode.connect(window.audioContext.destination);

          this.oscNode.start(window.audioContext.currentTime);
        }
      },
      onFreqChange() {
        // console.log(this.frequency);
        if (this.oscNode !== undefined)
          // this.oscNode.frequency.value = this.frequency;  // This doesn't work on Safari after oscNode started
          this.oscNode.frequency.setValueAtTime(this.frequency, window.audioContext.currentTime);
      },
      onFilterFreqChange() {
        // console.log(this.filterFrequency);
        if (this.filterNode !== undefined)
          this.filterNode.frequency.setValueAtTime(this.filterFrequency, window.audioContext.currentTime);

      },
      onGainChange() {
        console.log(this.gain);
        if (this.wetFilterGainNode !== undefined)
        {
          this.wetFilterGainNode.gain.setValueAtTime(this.gain, window.audioContext.currentTime);
          this.dryGainNode.gain.setValueAtTime(1 - this.gain, window.audioContext.currentTime);
        }
      },
      playFile: async function () {
        this.playingFile = true;
        const res = await fetch("test.mp3");
        console.log(res);
        const blob = await res.arrayBuffer()
        console.log(blob);
        const that = this
        window.audioContext.decodeAudioData(blob, function (buffer) {     
          that.playSoundNode = window.audioContext.createBufferSource();
          that.playSoundNode.onended = function() {
            that.playSoundNode = undefined;
            that.playingFile = false;
          };
          that.playSoundNode.buffer = buffer;
          that.playSoundNode.connect(window.audioContext.destination);
          that.playSoundNode.start(window.audioContext.currentTime);
        })
      },
      stopFile: function () {
          this.playingFile = false;
          if (this.playSoundNode === undefined)
            return;
          this.playSoundNode.stop();
          this.playSoundNode = undefined;
      }
    },

    created: function () {
      // let AudioContext = window.AudioContext || window.webkitAudioContext;
      // this.audioContext = new AudioContext();
      this.oscNode = undefined;
      this.playSoundNode = undefined;
      this.wetFilterGainNode = undefined;
      this.dryGainNode = undefined;
      this.filterNode = undefined;
      this.gainMixerNode = undefined;
  }
  }
</script>

